<template>
	<view class="container">


		<!-- 		<uni-swipe-action ref="swipeAction">
			<uni-swipe-action-item v-for="(item, index) in swipeList" :right-options="item.options" :key="item.id"
				@change="swipeChange($event, index)" @click="swipeClick($event, index)">
				<view class="content-box">
					<text class="content-text">{{ item.content }}</text>
				</view>
			</uni-swipe-action-item>
		</uni-swipe-action> -->

		<uni-swipe-action ref="swipeAction">
			<uni-swipe-action-item v-for="(item, index) in swipeList" :right-options="item.options" :key="item.id"
				@click="swipeClick($event, index)">
				<view class="content-box">
					<view class="todo_card">
						<img src="../../static/img/checkbox_unchecked.png" v-if="check" @click="chencBox">
						<img src="data:image/png;base64," v-if="!check" @click="chencBox">
						<text>测试文字测试撒打了卡久等啦看到啦凯撒的</text>
					</view>
				</view>
			</uni-swipe-action-item>
		</uni-swipe-action>

	</view>
</template>

<script>
	export default {
		components: {},
		data() {
			return {
				// show: false,
				// isOpened: 'none',
				check: true,
				swipeList: [{
					id: 0,
					options: [{
						text: '删除',
						isImage: true,
						src: 'data:image/png;base64,',
						style: {
							backgroundColor: 'rgb(255,58,49)',
							imgWidth: '64rpx',
							imgHeight: '72rpx'
						}
					}],
					content: 'item2'
				}, ]
			};
		},
		onReady() {
			// 模拟延迟赋值
			setTimeout(() => {
				this.isOpened = 'right';
			}, 1000);

			uni.$on('update', res => {
				console.log(111);
				this.swipeClick({
					content: {
						text: '添加'
					}
				})
			})
		},
		methods: {
			chencBox() {
				this.check = !this.check
			},
			swipeClick(e, index) {
				let {
					content
				} = e;
				if (content.text === '删除') {
					uni.showModal({
						title: '提示',
						content: '是否删除',
						success: res => {
							if (res.confirm) {
								this.swipeList.splice(index, 1);
							} else if (res.cancel) {
								console.log('用户点击取消');
							}
						}
					});
				} else {
					uni.showToast({
						title: `点击了${e.content.text}按钮`,
						icon: 'none'
					});
				}
			}
		}
	};
</script>

